
import { html, createHoc } from './ui';

export function createClient(args) {

  const {
    Store,
    lib,
    components,
    fragments,
  } = args;

  // Initialization
  const client = {
    html,
    ui: {},
  };

  // Store is necessary to create the HOC
  client.hoc = createHoc(Store, client);

  // Attach lib for convenience
  client.lib = lib;

  // Components will be available to fragments and pages
  client.ui.components = Object.keys(components).reduce((acum, key) => {
    return {
      ...acum,
      [key]: components[key](client),
    };
  }, {});

  // Fragments will be available to pages
  client.ui.fragments = Object.keys(fragments).reduce((acum, key) => {
    return {
      ...acum,
      [key]: fragments[key](client),
    };
  }, {});

  return client;

}

