import { processStyle } from './processStyle';export function getClasses(registerNumber, componentDefClasses = {}) {  const classKeys = Object.keys(componentDefClasses);  const mapping = {};  const rules = classKeys.reduce((acum, key) => {    const newClassName = `${key}--${registerNumber}`;    mapping[key] = newClassName;    return `${acum}${processStyle(newClassName, componentDefClasses[key])}`;  }, '');  return {    rules,    mapping,  };}