import globals from '../../globals';export function getDataFromServer(Store) {  return args => {    const {      instance = null,      user_id,      lastTimestamp = 0,      localEntitiesIds = [],    } = args;    const requestArgs = {      user_id,      lastTimestamp,      localEntitiesIds,    };    return new Promise((resolve, reject) => {      // Special method available ony for internal libs      Store.methods.callServerMethod(globals.GET_MODEL_DATA_METHOD, requestArgs)      .then(items => {        if (!items) reject('Could not get data from server');        const newTimestamp = Date.now();        resolve({ instance, items, newTimestamp });      })      .catch(reject);    });  };}