export function getFinalProps(args) {  const {    client,    store,    componentDef,    classesMapping,    processedStyles,  } = args;  const {    state = () => ({}),    actions = () => ({}),  } = componentDef;  const classes = {    ...Object.keys(classesMapping).reduce((acum, key) => {      return { ...acum, [key]: classesMapping[key] };    }, {}),  };  const sections = {    ...Object.keys(componentDef.sections || {}).reduce((acum, key) => {      return {        ...acum,        [key]: componentDef.sections[key](client),      };    }, {}),  };  return (props = {}) => {    return {      props,      state: state(props, store),      actions: actions(props, store),      styles: processedStyles,      classes,      sections,    };  };}